<template>
  <div class="body">
    <div class="back">
      <span @click="back()">返回</span>
          <aplayer :audio="audio" :lrcType="1" />
    </div>
    <div class="title">
      <div class="name">{{name}}</div>
      <div class="pic_box">
        <img :src="pic" alt />
      </div>
    </div>

    <div class="cont">
      <div class="geci">{{the_song}}</div>
    </div>
  </div>
</template>
<script>
import axios from "../axios/instance";
export default {
  name: "info",
  data() {
    return {
      song_id: this.$route.query.id,
      the_song: "",
      pic: "",
      name: "",
      cme:'',
        audio: {
        name: '东西（Cover：林俊呈）',
        artist: '',//名字
        url: [],//歌曲播放路径
        cover: '', // 图片
        lrc: '',//歌词
      },
    };
  },
  created() {
    this.geci();
    this.img();
    this.yin()
  },
  methods: {
    geci() {
      axios({
        url: "/lyric?id=" + this.song_id,
        method: "post"
      })
        .then(res => {
          
          this.the_song = res.data.lrc.lyric.replace(/\[.*?\]/g, "");
           this.audio.lrc= res.data.lrc.lyric//把axios里面拿到的歌词传进去进行显示
           console.log()
        })
        .catch(err => {
          console.log(err);
        });
    },
    img() {
      axios({
        url: "/song/detail?ids=" + this.song_id,
        method: "post"
      })
        .then(res => {
          console.log(res.data);
          this.name = res.data.songs[0].name;
     this.audio.artist=res.data.songs[0].name//把axios里面拿到的名字传进去进行显示
                  console.log('------------------'+res.data.songs[0].name)
          this.pic = res.data.songs[0].al.picUrl;
        this.audio.cover=this.pic//把axios里面拿到的图片传进去进行显示
          console.log(this.pic);
        })
        .catch(err => {
          console.log(err);
        });
    },back(){
    this.$router.go(-1);//返回上一页
    },
    yin(){
       axios({
        url: "/song/url?id="+this.$route.query.id ,
        method: "post"
      })
        .then(res => {
          console.log(res.data);
         this.audio.url.push(res.data.data[0].url)//把axios里面拿到的MP3路径传进去进行播放
              
     
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
<style scoped>
.body {
  width: 100%;
  height: 100vh;
  /* background-color: rgba(255, 170, 183, 0.219); */
  padding: 0;
  margin: 0;
  background: linear-gradient(
    to bottom,
    rgba(238, 163, 175, 0.664),
    rgba(211, 99, 116, 0.295),
    rgba(255, 187, 197, 0.219)
  );
}
.geci {
  white-space: pre-wrap;
  line-height: 35px;
  font-size: 17px;
  color: rgba(0, 0, 0, 0.644);
}
.pic_box {
  width: 65%;
  height: 210px;
  border-radius: 10px;
  margin: 0 auto;
  overflow: hidden;
  /* border: 1px solid black; */
}
.pic_box img {
  width: 100%;
  height: 210px;
}
.name {
  /* background:wheat; */
  text-align: center;
  background: linear-gradient(to right, rgb(230, 146, 160), rgb(207, 106, 125));
  color: transparent;
  -webkit-background-clip: text;
  font-size: 28px;
  font-weight: bold;
  padding: 5px 10px;
  padding-top: 60px;
  padding-bottom: 20px;
  opacity: 0;
}
.cont {
  padding: 10px;
  width: 85%;
  margin: 30px auto;
  border-radius: 20px;
  background: linear-gradient(
    to top,
    rgb(248, 238, 238),
    rgb(255, 227, 233),
    rgb(255, 203, 214)
  );
  box-shadow: 0 10px 16px rgba(218, 111, 111, 0.363);
  height: 47vh;
  overflow: scroll;
  animation: turn 1s linear;
}
.title{
    animation: turn 1s linear;
}
.back{
  padding:0 10px ;
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  /* background: white; */
  color: white;
}
@keyframes turn {
  0% {
    opacity: 0;
    transform: rotateX(-90deg) translate3d(0, 30px, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: rotate(0deg) translate3d(0, 0, 0);
  }
}
</style>